import {TimelineMax as Timeline, Power1} from 'gsap';

const getHomeTimeline = (node, delay) => {
    const timeline = new Timeline({ paused: true });
    // const texts = node.querySelectorAll('h1');
    console.log(document);
    console.log(document.body);
    console.log(document.body.querySelector("section"));
    console.log(document.body.querySelector("section").querySelectorAll('h1'));
    const texts = document.body.querySelector("section").querySelectorAll('h1');
    console.log(texts);    

    timeline
        .from(node, 0, { display: 'none', autoAlpha: 0, delay })
        .staggerFrom(texts, 0.375, { autoAlpha: 0, x: -25, ease: Power1.easeOut }, 0.125);

    return timeline
}

const getDefaultTimeline = (node, delay) => {
    const timeline = new Timeline({ paused: true });
    // const texts = node.querySelectorAll('h1');
    console.log(document);
    console.log(document.body);
    console.log(document.body.querySelector("section"));
    console.log(document.body.querySelector("section").querySelectorAll('h1'));
    const texts = document.body.querySelector("section").querySelectorAll('h1');
    console.log(texts);


    timeline
        .from(node, 0, { display: 'none', autoAlpha: 0, delay })
        .staggerFrom(texts, 0.375, { autoAlpha: 0, x: -25, ease: Power1.easeOut }, 0.125);

    return timeline
}

const play = (pathname, node, appears) => {    
    const delay = appears ? 0 : 0.5
    let timeline
    console.log(pathname, node, appears);

    if (pathname === '/')
        timeline = getHomeTimeline(node, delay)
    else
        timeline = getDefaultTimeline(node, delay)

    timeline.play()
}

export default play